import back from '@/components/back'
import noData from '@/components/noData'

export default {
	components: {
		back,
		noData
	},
	data() {
		return {
			title: '优选推荐记录',
			user: this.$store.state.userInfo.Data,
			haveMore: true,
			page: 1,
			list: [],
			reason: [],
			pageShow: false,
			recomList:[{
				val:1,
				name:"我的推荐"
			},{
				val:2,
				name:"他人推荐"
			}],
			sorList: ['日期不限', '交易状态'],
			dateList: [{
					name: '日期不限',
					val: '-1'
				},
				{
					name: '最近一周',
					val: '1'
				},
				{
					name: '最近一个月',
					val: '2'
				},
				{
					name: '最近半年',
					val: '3'
				},
				{
					name: '最近一年',
					val: '4'
				}
			],
			buyList: [{
					name: '全部状态',
					val: '-1'
				},
				{
					name: '未支付',
					val: '0'
				},
				{
					name: '已支付',
					val: '2'
				},
				{
					name: '已退款',
					val: '5'
				},
				{
					name: '已关闭',
					val: '8'
				}
			],
			date: {
				name: '日期不限',
				val: '-1'
			},
			status: {
				name: '全部状态',
				val: '-1'
			},
			buy: {
				name: '全部状态',
				val: '-1'
			},
			idx: null,
			search: '',
			inputFlag: false,
			reIdx:{
				val:1,
				name:"我的推荐"
			},
			show3: false,
			loading: false,
			scrollTopDistance:0,
		}
	},
	created() {
		this.pull(false)
	},
	mounted() {
		this.getMore()
		this.reload()
		this.scrollIos()
	},
	beforeRouteLeave(to,from,next){
        window.onscroll = null
	    if(from.meta.keepAlive) {
	　       from.meta.scollTopPosition = this.scrollTopDistance; 
	    }
	    next()
    },
    beforeRouteEnter(to, from, next) {  //进入前    	
	    next(vm => {
            vm.getMore()
            setTimeout(()=>{
		        vm.$refs.el3.scrollTop = to.meta.scollTopPosition;   
		    },1)
	    });
	},
	beforeDestroy() {
		window.onscroll = null
	},
	watch: {
		idx(n) {
			if(n === null) {
				this.$refs.el3.style = 'overflow-y: auto'
			} else {
				this.$refs.el3.style = 'overflow-y: hidden;'
			}
		}
	},
	methods: {
		searchFor() {
			if(this.inputFlag) {
				return
			}
			this.page = 1
			this.haveMore = true
			this.pull(false)
		},
		pull(h) {
			if(this.haveMore) {
				this.$post('PlatFormAPI/SpecialistHosGroup/GetGoodsRecommendListForDoctor', {
					DrId: this.user.User.DoctorId,
					DateRangeType: this.date.val,
					PayStatus: this.buy.val,
					PageIndex: this.page,
					PageSize: 15,
					SearchContent: this.search,
					RecommendRelation:this.reIdx.val
				}).then(o => {
					this.pageShow = true
					this.show3 = false
					this.loading = false
					o.ReturnData = o.ReturnData || []
					if(!o.ReturnData || o.ReturnData.length < 15) {
						this.haveMore = false
					} else {
						this.haveMore = true
					}
					if(h) {
						o.ReturnData.forEach(v => {
							this.list.push(v)
						})
					} else {
						this.$refs.el3.scrollTop = 0
						this.list = o.ReturnData || []
						this.$nextTick(() => {
							window.scrollTo(0, 5)
						})
					}
				})
			}
		},
		sort(idx) {
			if(this.idx == idx) {
				this.idx = null
			} else {
				this.idx = idx
			}
		},
		sortRecom(n){
			this.reIdx = n
			this.haveMore = true
			this.page = 1
			this.pull(false)
		},
		chose(i) {
			this.date = i
			this.sorList[this.idx] = i.name
			this.idx = null
			this.haveMore = true
			this.page = 1
			this.pull(false)
		},
		chose2(i) {
			this.buy = i
			this.sorList[this.idx] = i.name
			this.idx = null
			this.haveMore = true
			this.page = 1
			this.pull(false)
		},
		/*payLink(i){
			//付款链接
		},*/
		goArchives(i) {
			let obj = {}
			obj = {
				"Birthday": "",
				"PatientName": i.PatientName,
				"PatientId": i.PatientId,
				"AreaId": null,
				"AccountId": i.AccountId || null,
				"PatientSex": i.PatientSex,
				"AreaName": null,
				"Age": i.PatientAge,
			}
			this.$store.dispatch('pushpatient', obj)
			this.$router.push("/archives")
		},
		getMore() {
			const el3 = this.$refs.el3
			let timer2 = null
			el3.onscroll = () => {
				clearTimeout(timer2)
				timer2 = setTimeout(() => {
					const top = el3.scrollTop ||
						el3.pageYOffset ||
						el3.scrollTop,
						h = el3.scrollHeight,
						wh = window.innerHeight
					this.scrollTopDistance = top;
					if(h - top - wh < 100 && this.haveMore) {
						this.page += 1
						this.loading = true
						this.pull(true)
					}
				}, 100)
			}
			/*const el = this.$refs.el
			let timer = null
			window.onscroll = () => {
				clearTimeout(timer)
				timer = setTimeout(() => {
					const top = document.documentElement.scrollTop ||
						window.pageYOffset ||
						document.body.scrollTop,
						h = el.scrollHeight,
						wh = window.innerHeight

					if(h - top - wh < 100 && this.haveMore) {
						this.page += 1
						this.pull(true)
					}
				}, 100)
			}*/
		},
		reload() {
			const el2 = this.$refs.el2
			const el3 = this.$refs.el3
			let startX = null
			let startY = null
			let endX = null
			let endY = null
			el2.addEventListener('touchstart', e => {
				this.show3 = false
				startX = e.changedTouches[0].pageX
				startY = e.changedTouches[0].pageY
			})
			el2.addEventListener('touchmove', e => {
				endX = e.changedTouches[0].pageX
				endY = e.changedTouches[0].pageY
				if(this.idx !== null) {
					return
				}
				if(Math.abs(startX - endX) < Math.abs(startY - endY) && (startY - endY) < 0) {
					const top = el3.scrollTop ||
						el3.pageYOffset
					if(top < 10 || !top) {
						this.show3 = true
					}
				}
			})
			el2.addEventListener('touchend', () => {
				if(this.show3) {
					this.page = 1
					this.haveMore = true
					this.pull(false)
				}
			})
		},
		scrollIos(){
			//ios页面卡死问题
			let el3 = this.$refs.el3
			let lastY = 0;
			el3.addEventListener('touchstart', function(event) {
				lastY = event.touches[0].clientY;
			});
			//获取滚动元素
			let scrollEle =this.$refs.el3
			//先移除监听事件
			scrollEle.removeEventListener('touchmove', function (event) {
				event.preventDefault();
			}, false);
			//再添加监听事件
			scrollEle.addEventListener('touchmove', function(event) {
				let top = event.touches[0].clientY;
				let scrollTop = scrollEle.scrollTop;
				let scrollHeight = scrollEle.scrollHeight;
				let clientHeight = scrollEle.clientHeight;
				let direction = (lastY - top) < 0 ? "up" : "down";
				if (scrollTop == 0 && direction == "up") {
				  event.preventDefault();
				} else if (scrollTop >= (scrollHeight - clientHeight) && direction == "down") {
				  event.preventDefault();
				}
				lastY = top;
			});
		  
		}
	}
}



